<template>
  <div class="manual_settings border px-3 mx-3">
    <div class="d-flex flex-row py-3">
      <div class="item-label">Laser Mode</div>
      <div>
        <b-input-group>
          <b-form-input
            size="lg"
            class="centered-text"
            :disabled="!enabled"
            v-model="laser_mode"
            type="number"
            min="0"
            max="16"
            text-align="center"
            @click="()=>show_keyboard=true"
          ></b-form-input>
          <div class="updown-btns">
            <b-input-group-append>
              <b-button :disabled="!enabled" squared @click="LaserMode_Increase"></b-button>
            </b-input-group-append>
            <b-input-group-prepend>
              <b-button :disabled="!enabled" squared @click="LaserMode_decrease"></b-button>
            </b-input-group-prepend>
          </div>
        </b-input-group>
      </div>
      <b-button
        @click="modifyLaserModeDialogShow=true"
        :disabled="!enabled"
        squared
        class="mx-1"
        variant="primary"
      >Modify</b-button>
    </div>
    <el-drawer v-model="show_keyboard" direction="btt" size="50%">
      <SimpleKeyboard keyboard_type="number" @onChange="onChange"></SimpleKeyboard>
    </el-drawer>

    <div v-if="IsBatteryLockControlable" class="d-flex flex-row py-3">
      <div class="item-label"></div>
      <div class="battery py-1">
        <div class="d-flex flex-row mb-1">
          <label> 1</label>
          <b-button @click="BatteryLockHandler(1,true)" squared variant="primary">Lock</b-button>
          <b-button @click="BatteryLockHandler(1,false)" squared variant="danger">Unlock</b-button>
        </div>
        <div class="d-flex flex-row">
          <label> 2</label>
          <b-button @click="BatteryLockHandler(2,true)" squared variant="primary">Lock</b-button>
          <b-button @click="BatteryLockHandler(2,false)" squared variant="danger">Unlock</b-button>
        </div>
      </div>
    </div>
    <div v-if="IsForkARMControlable" class="d-flex flex-row py-3">
      <div class="item-label"></div>
      <div class="battery py-1">
        <div class="d-flex flex-row mb-1">
          <b-button
            :disabled="!FORK_ARM_Status.IsArmAtHomePose&&FORK_ARM_Status.IsArmAtEndPose"
            @click="ForkArmPoseControlHandler(false)"
            squared
            variant="primary"
          ></b-button>
          <b-button
            :disabled="!FORK_ARM_Status.IsArmAtEndPose&&FORK_ARM_Status.IsArmAtHomePose"
            @click="ForkArmPoseControlHandler(true)"
            squared
            variant="primary"
          ></b-button>
          <b-button @click="ForkArmStopHandler()" squared variant="danger"></b-button>
        </div>
      </div>
    </div>
    <div v-if="false" class="d-flex flex-row py-3">
      <div class="item-label"></div>
      <b-button
        :disabled="!enabled"
        class="mx-1"
        squared
        variant="danger"
        style="width:130px"
        @click="Brake()"
      ></b-button>
      <b-button
        :disabled="!enabled"
        class="mx-1"
        squared
        variant="primary"
        style="width:130px"
        @click="UnBrake()"
      ></b-button>
    </div>

    <div v-if="false" class="d-flex flex-row py-3">
      <div class="item-label"></div>
      <b-button
        :disabled="!enabled"
        class="mx-1"
        squared
        variant="danger"
        style="width:130px"
        @click="ResetMile()"
      ></b-button>
    </div>
    <b-modal
      v-model="modifyLaserModeDialogShow"
      :centered="true"
      title="Laser Mode Change"
      @ok="ModifyLaserMode"
    >
      <p>Change Laser Mode to : {{laser_mode }}</p>
      <p>Are you sure?</p>
    </b-modal>
  </div>
</template>

<script>
import { LaserMode, Braker, Reset_Mileage, BatteryLockCtrl, ForkAPI } from '@/api/VMSAPI.js'
import Notifier from '@/api/NotifyHelper';
import SimpleKeyboard from '@/components/Tools/SimpleKeyboard.vue'
import { AGVStatusStore, DIOStore } from '@/store'

export default {
  components: {
    SimpleKeyboard,
  },
  props: {
    enabled: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      laser_mode: 0,
      modifyLaserModeDialogShow: false,
      show_keyboard: false

    }
  },
  computed: {
    IsBatteryLockControlable() {
      return AGVStatusStore.getters.IsInspectionAGV
    },
    IsForkARMControlable() {
      return AGVStatusStore.getters.IsForkAGV
    },
    FORK_ARM_Status() {
      //{
      //   IsArmAtHomePose: IsArmAtHomePose,
      //   IsArmAtEndPose: IsArmAtEndPose
      // }

      return DIOStore.getters.Fork_ARM_States
    }
  },
  methods: {
    LaserMode_Increase() {
      this.laser_mode++;
    },
    LaserMode_decrease() {
      if (this.laser_mode == 0)
        return;
      this.laser_mode--;
    },
    async ModifyLaserMode() {
      await LaserMode(this.laser_mode);
      Notifier.Success(`Lasr mode changed:${this.laser_mode}`);
    },
    async Brake() {
      await Braker.Brake();
    },
    async UnBrake() {
      await Braker.UnBrake();

    },
    async ResetMile() {
      await Reset_Mileage();
    },
    async BatteryLockHandler(bat_no, islock) {
      await BatteryLockCtrl(bat_no, islock)
    },
    async ForkArmPoseControlHandler(isExtend) {
      if (isExtend)
        await ForkAPI.ARM_Extend();
      else
        await ForkAPI.ARM_Shorten();
    },
    async ForkArmStopHandler() {
      await ForkAPI.ARM_Stop();
    },
    onChange(input) {
      if (input + "" == "") {
        this.laser_mode = 0;
      } else {
        if (input > 16) {
          input = 16
        }
        this.laser_mode = input;
      }
    }
  },
}
</script>

<style lang="scss" scoped>
.manual_settings {
  button {
    font-size: 24px;
    font-weight: bold;
    width: 130px;
  }
  height: 450px;
  .item-label {
    width: 160px;
    font-size: 24px;
    text-align: left;
    padding: 5px;
  }
  .b-form-input input {
    text-align: center;
  }
  .updown-btns {
    button {
      height: 25px;
      font-size: small;
      text-align: center;
      padding: 0px;
      width: 43px;
      border: 1px solid rgb(255, 255, 255);
      background-color: rgb(0, 123, 255);
    }
  }
  .battery {
    font-size: 25px;
    label {
      margin-right: 10px;
    }
    button {
      margin: auto 5px;
    }
  }
  .el-overlay {
    background-color: transparent;
  }
}
</style>