import clsDriverState from './clsDriverState'
import BatteryStatus from './BatteryStatus'
class VMSData {
  APPVersion = "1.0.0"
  Agv_Type = 0
  AutoMode = 0
  AGVC_ID = -1
  AngularSpeed = 0
  Angle = 0
  AlarmCodes = []
  AGV_Direct = 'STOP'
  BCR_State_MoveBase = {
    state: 0,
    tagID: 0,
    xValue: 0.0,
    yValue: 0.0,
    theta: 0.0,
  }
  BatteryStatus = new Array() < BatteryStatus > 0

  CarName = 'AGV'
  CST_Data = ''
  Current_LASER_MODE = 'Bypass'
  DriversStates = new Array() < clsDriverState > 0
  IsAGVPoseError = false
  IsInitialized = false
  IsSystemIniting = false
  LinearSpeed = 0
  Laser_Mode = 0
  Last_Visited_Tag = 1
  Last_Visit_MapPoint = { Name: 'UNKNOWN' }
  LocStatus = 10
  LightsStates = {
    Front: false,
    Back: false,
    Right: false,
    Left: false,
    Run: false,
    Down: false,
    Idle: false,
    Online: false,
  }
  MapComparsionRate = -1
  Mileage = 0
  MainState = 'DOWN'

  NavInfo = {
    Destination: '',
    Speed_max_limit: -1,
    PathPlan: [],
    DestinationMapPoint: {
      Name: 'UNKNOWN'
    }
  }
  NewestAlarm = undefined
  OnlineMode = 0
  Pose = {
    position: {
      x: 0,
      y: 0,
      z: 0,
    },
    orientation: {
      x: 0,
      y: 0,
      z: 0,
      w: 0,
    },
  }
  Simulation = false
  SubState = 'DOWN'
  Tag = -1
  UltrSensorState = new UltrasonicSensorState()
  ZAxisDriverState = new clsDriverState()
  ZAxisActionName = ''

}

export class UltrasonicSensorState {
  state = 1
  errorCode = 1
  errorString = ''
  dirFront = 0
  dirRear = 0
  dirLeft = 0
  dirRight = 0
}

export class AlarmCode {
  Code = 0
  Description = ''
  CN = ''
  EAlarmCode = 0
}

export default VMSData
